body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #FF9F1C;
  --main-color-text: #623a03;
  --secondary-color: #419f3e;
  --dark: #011627;
  --bright: #FDFFFC;
}

html, body {   background: #419f3e;   background: var(--secondary-color);}

.App {
  text-align: center;
  background: #419f3e;
  background: var(--secondary-color);
}

.wrapper {
  max-width: 500px;
  margin: 0px auto;
  background: #FDFFFC;
  background: var(--bright);
  color: #419f3e;
  color: var(--secondary-color);
}
header { 
  overflow-x: hidden;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

header h1 {
  color: #fff;
  background: #419f3e;
  background: var(--secondary-color);
  text-shadow: 1px 1px 0px #419f3e, 1px 1px 3px #184e16;
  text-shadow: 1px 1px 0px var(--secondary-color), 1px 1px 3px #184e16;
  margin: 0px;
  text-align: center;
  text-transform: uppercase;
}

main .explainer-text {
  padding: 15px;
  font-size: 1.5em;
  font-weight: 800;
  color: #011627;
  color: var(--dark);
}

main input {
  border-radius: 8px;
  border: none;
  padding: 15px;
  margin: 0px 15px 15px 15px;
  width: calc(100% - 60px);
  max-width: 400px;
  color: #011627;
  color: var(--dark);
  font-size: 1em;
  font-weight: 800;
  background: #E0E0E0;
}

button.primary-btn {
  cursor: pointer;
  padding: 15px;
  background: #FF9F1C;
  background: var(--main-color);
  color: #623a03;
  color: var(--main-color-text);
  border-radius: 8px;
  margin: 0px 15px 15px 15px;
  border: none;
  font-size: 1em;
  font-weight: 800;
  width: calc(100% - 30px);
  max-width: 400px;
  text-transform: uppercase;
}

.vraag-wrapper {
  padding: 30px 0px 15px 0px;
}

.vraag-wrapper .vraag-text {
  padding: 0px 15px 15px 15px;
  font-size: 1.5em;
  font-weight: 800;
  display: block;
  color: #011627;
  color: var(--dark);
}
body {
    font-family: Arial, sans-serif;
    background-color: #f5f5f5;
  }
  
  header {
    background-color: #333;
    color: white;
    padding: 20px;
    text-align: center;
  }
  
  h1 {
    margin: 0;
  }
  
  main {
    padding: 20px;
  }
  
  form {
    background-color: white;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    max-width: 500px;
    margin: 20px auto;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  input[type="text"],
  input[type="password"] {
    padding: 8px;
    font-size: 16px;
    width: 100%;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button[type="submit"] {
    background-color: #333;
    color: white;
    padding: 8px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    float: right;
  }
  
  button[type="submit"]:hover {
    background-color: #444;
  }
  
  .wrapper {
    max-width: 800px;
    margin: 0 auto;
  }
  
